import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { HDMService } from './service/hdm.service';
import { ReadInputService } from './service/read-input.service';
import { MainComponent } from './main/main.component';
import { HttpModule } from "@angular/http";
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NoopAnimationsModule } from '@angular/platform-browser/animations';
import { MatButtonModule, MatCheckboxModule, MatToolbarModule, MatAccordion, MatExpansionModule, MatInputModule, MatIconModule, MatError, MatMenuModule, MatDialogModule, MatTableModule } from '@angular/material';
import { FormsModule } from '@angular/forms';
import { DrivesNavComponent } from './drives-nav/drives-nav.component';
import { MainRouterComponent } from './main-router/main-router.component';
import { MainDriveComponent, DialogChangeValue } from './main-drive/main-drive.component';
import { AppRoutingModule } from './app-routing.module';
import { ConfigTagsComponent } from './config-tags/config-tags.component';

@NgModule({
  declarations: [
    AppComponent,
    MainComponent,
    DrivesNavComponent,
    MainRouterComponent,
    MainDriveComponent,
    DialogChangeValue,
    ConfigTagsComponent
  ],
  entryComponents: [DialogChangeValue],
  imports: [
    BrowserModule,
    HttpModule,
    BrowserAnimationsModule,
    NoopAnimationsModule,
    MatButtonModule, MatCheckboxModule, MatToolbarModule, MatExpansionModule, MatInputModule, MatIconModule, FormsModule, MatMenuModule
    , AppRoutingModule, MatDialogModule, MatTableModule
  ],
  providers: [HDMService, ReadInputService],
  bootstrap: [AppComponent]
})
export class AppModule { }



