import { Component, OnInit, Inject } from '@angular/core';
import { ReadInputService } from '../service/read-input.service';
import { Drive } from '../model/drive';
import { Resource } from '../model/resource';
import { HDMService } from '../service/hdm.service';
import { Variable } from '../model/variable';
import { ActivatedRoute } from '@angular/router';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material'

@Component({
  selector: 'app-main-drive',
  templateUrl: './main-drive.component.html',
  styleUrls: ['./main-drive.component.css']
})
export class MainDriveComponent implements OnInit {
  dIndex: number = 0;



  constructor(private readInputService: ReadInputService, private hdmservice: HDMService, private route: ActivatedRoute, public dialog: MatDialog) { }
  drives: Array<Drive>;
  ngOnInit() {
    this.getDrivesFromService();
    this.dIndex = 0;
    this.route.params.subscribe(params => {
      this.dIndex = params['drv']; // (+) converts string 'id' to a number
      if (this.drives && this.dIndex)
        this.resource = this.drives[this.dIndex].resources[0];
      this.openPanel = '';
      // In a real app: dispatch action to load the details here.
    });
  }

  getDrivesFromService() {
    this.readInputService.getDrives().subscribe(data => {
      let drivesFromJson = data.json().drives as Array<Drive>;
      //console.log(drivesFromJson);
      this.drives = Drive.getDrivesFromJson(drivesFromJson);
      // console.log(this.drives);
      // this.drives.forEach(element => {
      //   console.log(element.getHDMReadXml());
      //   console.log(element.getUrl());
      // });
      if (this.drives && this.dIndex) {
        this.resource = this.drives[this.dIndex].resources[0];
        this.inputHdmString = this.resource.getHDM();
        this.getValuesFromHDM();
      }
    });

  }







  //////////////////
  hdm: any;
  resource: Resource;
  inputHdmString: string;
  openPanel: string = '';


  getValuesFromHDM(fromTimeout = 0) {

    this.hdmservice.Call_HDM('', this.inputHdmString).subscribe(data => {
      var str = data._body;
      this.resource.variables.forEach(element => {
        var prefix = '"' + element.id + '">';
        var re = new RegExp(prefix + "-?\\d*[.]?\\d*", "gi");
        var value = str.match(re)[0].replace(prefix, '');
        if (value) element.value = value;
        if (fromTimeout == 0) element.setValue = value;
      });
      //console.log(this.resource.getHDM());
      setTimeout(() => this.getValuesFromHDM(1), 1000);
    });
  }

  getshowhide(v: Variable) {
    if (v.canWrite) return '';
    else return 'false';
  }

  setValue(v: Variable) {
    var writeHdmString = this.resource.getHDMWrite(v);
    this.hdmservice.Call_HDM('', writeHdmString).subscribe(data => { this.openPanel = ''; console.log(data._body) });
    //console.log(writeHdmString);
  }


  ////////////////
  animal: string;
  name: string;
  openDialog(v: Variable): void {
    if (!v.canWrite) return;
    v.setValue = v.value;
    let dialogRef = this.dialog.open(DialogChangeValue, {
      //width: '310px',
      data: v
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(v);
      // this.animal = result;
    });
  }

}
////////////////
@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'dialog-component.html',
})
export class DialogChangeValue {

  constructor(
    public dialogRef: MatDialogRef<DialogChangeValue>,
    @Inject(MAT_DIALOG_DATA) public data: Variable) { console.log(data.canWrite) }

  onNoClick(): void {
    this.dialogRef.close();
  }

  getErrorText(v: Variable) {
    if (v.value == v.setValue) return '  ';
    if (v.maxValue == v.minValue) return '';
    if (v.setValue < v.minValue) return 'Min value is ' + v.minValue;
    if (v.setValue > v.maxValue) return 'Max value is ' + v.maxValue;
    return '';
  }


}


