import { Resource } from "./resource";
import { Variable } from "./variable";

export class Drive {
    driveDisplayText: string;
    ipAddress: string;
    resources: Resource[];

    constructor(driveDisplayText, ipAddress) {
        this.driveDisplayText = driveDisplayText;
        this.ipAddress = ipAddress;
        this.resources = Array<Resource>();
    }

    static getDrivesFromJson(data: Array<Drive>): Array<Drive> {
        var drives = Array<Drive>();
        data.forEach(driveFromJson => {
            var drive = new Drive(driveFromJson.driveDisplayText, driveFromJson.ipAddress);
            drive.resources = this.getResourcesFromDriveJson(driveFromJson.resources);
            drives.push(drive);
        });
        return drives;
    }

    static getResourcesFromDriveJson(data: Array<Resource>): Array<Resource> {
        var resources = Array<Resource>();
        data.forEach(resourceFromJson => {
            var resource = new Resource(resourceFromJson.resourceName, resourceFromJson.service);
            resourceFromJson.variables.forEach(element => {
                resource.variables.push(new Variable(element.displayText, element.dataType, element.id, element.canWrite, element.minValue, element.maxValue));
            });
            resources.push(resource);
        });
        return resources;
    }
    getUrl(): string {
        return 'http://' + this.ipAddress + '/peccgi/system/driver/HDMDRV?request=byPostData';
    }

    getHDMReadXml(): string {
        var resourcesXML = '';
        this.resources.forEach(element => {
            resourcesXML += element.getResource();
        });
        return '<hdm>' + resourcesXML + '</hdm>';
    }
}



