import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainDriveComponent } from './main-drive/main-drive.component'
import { ConfigTagsComponent } from './config-tags/config-tags.component';

const routes: Routes = [
    { path: '', pathMatch: 'full', redirectTo: 'mainDrive' },
    { path: 'mainDrive', component: MainDriveComponent },
    { path: 'mainDrive/:drv', component: MainDriveComponent },
    { path: 'configtag', component: ConfigTagsComponent },
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: true })],
    exports: [RouterModule]
})
export class AppRoutingModule { }



