import { Component, OnInit } from '@angular/core';
import { ReadInputService } from '../service/read-input.service';
import { Drive } from '../model/drive';

@Component({
  selector: 'app-drives-nav',
  templateUrl: './drives-nav.component.html',
  styleUrls: ['./drives-nav.component.css']
})
export class DrivesNavComponent implements OnInit {

  constructor(private readInputService: ReadInputService) { }
  drives: Array<Drive>;
  ngOnInit() {
    this.getDrivesFromService();
  }

  getDrivesFromService() {
    this.readInputService.getDrives().subscribe(data => {
      let drivesFromJson = data.json().drives as Array<Drive>;
      //console.log(drivesFromJson);
      this.drives = Drive.getDrivesFromJson(drivesFromJson);
      // console.log(this.drives);
      // this.drives.forEach(element => {
      //   console.log(element.getHDMReadXml());
      //   console.log(element.getUrl());
      // });
    });
  }

}



